@import "vars.scss";

.input {
  width: 100%;
  padding: 17px;
  margin-bottom: 16px;
  
  background: #f7f8ff;
  border: 1px solid $whiteColor;
  border-radius: 8px;

  &::placeholder {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 14px;

    color: #b9c1ce;
  }

  &-textarea {
    height: 125px;
    width: 100%;
    padding: 17px 0 0 17px;
    background: #f7f8ff;
    border: 1px solid $whiteColor;
    border-radius: 8px;

    resize: none;
  }

  &-textarea::placeholder {
    color: #b9c1ce;
    font-size: 14px;
    line-height: 14px;
    font-family: "Roboto", sans-serif;
  }
}
